import querystring from 'vux/src/tools/querystring/index.js'

import {removeToken, setToken} from './auth'

function getSession() {
let params = window.location.href.indexOf('?') !== -1?
window.location.href.split('?')[1].split('&'): ''
let search = {}

if (params && params.length >0) {
params.forEach(item => {
let flag = item.split('=')
search[flag[0]] = flag[1]
})
}

return search.session? search['session']: ''
}

/**
* 判断href有无session
* 有：设置cookies
* 无：清除cookies
*/
function initSession() {
let token = getSession()
token? setToken(token): removeToken(token)
}

initSession()


